 $switchWidth: 40px;
 $switchHeight: 20px;
 $padding: 4px;
 $innerSize: $switchHeight - 2 * $padding;
 $translationX: $switchWidth - $innerSize - 2 * $padding;

 /* The switch - the box around the slider */
 .switch {
  position: relative;
  display: flex;
  width:  $switchWidth;
  height: $switchHeight;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height:  $innerSize;
  width:  $innerSize;
  left:  $padding;
  bottom:  $padding;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX( $translationX);
  -ms-transform: translateX( $translationX);
  transform: translateX( $translationX);
}

/* Rounded sliders */
.slider.round {
  border-radius:  $switchHeight;
}

.slider.round:before {
  border-radius: 50%;
} 