$unit: 20px;
$resumeWidth: 960px;
$border-thickness: 2px;

:root {
  --background-contrast: #F2F2F3;
  --border-default: #1D1148;
  --background-default: white;
  --content-default: #1D1148;
}

body {
  background: var(--background-contrast);
  font-family: "Helvetica Neue";
  padding: 0;
  margin: 0;
  color: var(--content-default);
}

#main {
  margin-left: auto;
  margin-right: auto;
  max-width: $resumeWidth;
  background: var(--background-default);
}

.part {
  padding: $unit;

  a,
  a:visited {
    color: var(--content-default);
  }

  ul {
    list-style: none;
    padding: 0;

    li {
      margin: $unit / 2 0;
    }
  }
}

h1 {
  margin-top: 0;
  font-weight: bold;
}

#header-section {
  color: var(--content-default);

  #controls {

    float: right;
    display:flex;
    gap: $unit;
    align-items: center;

    #dark {
      display:flex;
      gap: $unit / 2;
      align-items: center;
    }
  }


  img {
    border-radius: 50%;
    width: 115px;
    height: 115px;
    float: left;
    margin-right: $unit;
    margin-bottom: $unit;
    background-color: white;
  }

  h1 {
    margin-bottom: 0;
  }

  h2 {
    font-weight: lighter;
    margin-top: 0;
  }

  .contact {


    display: flex;
    flex-flow: row wrap;
    border-top: 1px solid;
    border-bottom: 1px solid;
    border-color: var(--content-default);

    span {
      flex-grow: 1;
      padding: 10px 0;

      .fas,
      .fab {
        margin-right: $unit / 2;
      }
    }

    .location::before {
      content: "fas fa-home";
      font-family: "FontAwesome";
    }
  }

  .intro {
    margin-top: $unit;
    font-size: 1.5em;
    line-height: 150%;
  }
}

#skills-section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: min-content;
  column-gap: $unit;

  color: var(--content-default);

  h2 {
    padding-bottom: $unit / 2;
    border-bottom: $border-thickness solid var(--border-default);
  }
}

#experience-section {
  color: var(--content-default);

  .experience {
    display: grid;
    column-gap: 10px;
    grid-template-columns: 100px 1fr;
    grid-template-areas:
      "year title"
      ".... what";

    .title {
      grid-area: title;
      margin: 5px 0;
    }

    .year {
      grid-area: year;
      min-width: 80px;

      span {
        display: table;
        margin: 0 auto;
        border-radius: 5px;
        background-color: var(--background-contrast);
        color: var(--content-default);
        font-size: 0.8em;
        padding: $unit / 2;
        font-weight: bolder;
        text-align: center;
      }
      
    }

    .what {
      grid-area: what;

      ul {
        list-style: none;
        padding: 0;
        margin-bottom: 2 * $unit;
        margin-top: $unit / 2;

        li {
          margin-bottom: $unit / 4;
          font-size: 0.9em;
        }
      }
    }
  }


}

#diploma-section {

  .box {
    float: left;
    margin-right: $unit;
    margin-bottom: $unit;
    border-radius: 5px;
    background-color: var(--background-contrast);
    font-size: 0.8em;
    padding: $unit / 2;
    font-weight: bolder;
    min-width: 80px;
    text-align: center;
  }

  #disclaimer {
    border-top: 1px solid var(--border-default);
    width: 30%;
    margin: 2 * $unit auto;
    padding: 2 * $unit;
    text-align: center;
  }
}

.clear {
  clear: both;
}
